import React, { useState } from 'react'
import { Link } from 'react-router-dom'

interface NavBarProps {

}

const NavBar: React.FC<NavBarProps> = (props) => {
  const [navBar, setNavBar] = useState(false)
  return (
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
      <Link className="navbar-brand" to="/">Equipo Rojo</Link>
      <button className="navbar-toggler" onClick={() => setNavBar(!navBar)}>
        <span className="navbar-toggler-icon"></span>
      </button>

      <div className={`collapse navbar-collapse ${navBar ? 'show' : ''}`}>
        <ul className="navbar-nav mr-auto">
          <li className="nav-item">
            <Link className="nav-link" to="/docs">Docs</Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" to="/design">Diseño de la base de datos</Link>
          </li>
          <li className="nav-item dropdown">
            <button className="nav-link dropdown-toggle" id="toggle"
              onClick={() => document.getElementById('toggle')?.focus()}
              aria-haspopup="true">Tablas</button>

            <div className="dropdown-menu">
              <Link className="dropdown-item" to="/tabla/persona">Personas</Link>
              <Link className="dropdown-item" to="/tabla/empleado">Empleados</Link>
              <Link className="dropdown-item" to="/tabla/piloto">Pilotos</Link>
              <Link className="dropdown-item" to="/tabla/avion">Aviones</Link>
            </div>
          </li>
        </ul>
        <button className="btn btn-outline-primary">Log In</button>
      </div>
    </nav>
  )
}

export default NavBar