import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
interface TablaProps {
	nombreTabla: string
}
interface tabla {
	columnas: string[],
	filas: Array<string[]>
}
const Tabla: React.FC = () => {
	const API = 'https://equiporojoapi.herokuapp.com'
	const props = useParams<TablaProps>()

	const miTabla: tabla = {
		columnas: ['estos', 'son', 'datos de prueba'],
		filas: [['si', 'estas', 'viendo esto'], ['algo', 'salio', 'mal']]
	}
	const [tabla, setTabla] = useState(miTabla)


	useEffect(() => {
		fetch(`${API}/tabla/${props.nombreTabla}`)
			.then(response => response.json())
			.then(data => setTabla(data))
	}, [props])

	return (
		<>
			<h3 style={styles.titulo}>Tabla {props.nombreTabla}</h3>
			<div style={styles.tabla}>
				<table className="table table-hover table-secondary">
					<thead>
						<tr className="table-primary">
							{
								tabla.columnas.map((x, i) => (
									<th scope="col" key={i}>{x}</th>
								))
							}
						</tr>
					</thead>
					<tbody>
						{
							tabla.filas.map((fila, i) => (
								<tr key={i}>
									{
										fila.map((celda, i) => (
											<td key={i}>{celda}</td>
										))
									}
								</tr>
							))
						}
					</tbody>
				</table>
			</div>
			<div style={styles.arrow} role="button"></div>
		</>
	)
}

export default Tabla

interface StyleSheet {
	[nombre: string]: React.CSSProperties
}

const styles: StyleSheet = {
	tabla: {
		width: '80vw',
		height: '70vh',
		margin: 'auto',
		overflow: 'auto',
		marginBottom: '2vh'
	},
	titulo: {
		textAlign: 'center',
		marginTop: '5vh'
	},
	arrow: {
		position: 'relative',
		left: '50vw',
		height: '0',
		width: '0',
		border: '20px solid white',
		borderRightColor: 'transparent',
		borderLeftColor: 'transparent',
		borderBottomColor: 'transparent',
	},
}
