import React from 'react'

interface CardProps {
	image: string
	name: string
}

const Card: React.FC<CardProps> = (props) => {
	return (
		<li className="_card">
			<img src={props.image} width="50px" height="50px" alt="profile" />
			<p>{props.name}</p>
		</li>
	)
}
export default Card