import React from 'react'
import admin from '../images/admin.jpg'
import man_1 from '../images/man_1.jpg'
import man_2 from '../images/man_2.jpg'
import woman_1 from '../images/woman_1.jpg'
import postgresql from '../images/postgresql.png'
import Card from './Card'

interface HomeProps {

}

const Home: React.FC<HomeProps> = (props) => {
  return (
    <div className="_leftPanel">
      <h3>Proyecto:</h3>
      <p style={styles.parrafo}>
        El grupo realizó una base de datos para una escuela de aviación en PostgreSQL,
        con el objetivo de reforzar lo aprendido, para esto se utilizaron
        <strong> funciones de postgres</strong> <i>(procedimientos almacenados)</i>,
        <strong> transacciones</strong> y niveles de aislamiento de transacciones
      </p>
      <h4 className="mt-4">Integrantes:</h4>
      <ul>
        <Card image={admin} name="matthias marin vargas" />
        <Card image={man_1} name="carlos manuel claure vargas" />
        <Card image={woman_1} name="jessica carmen iriarte bustillo" />
        <Card image={man_2} name="walter abdon bautista poma" />
      </ul>
      <img className="logo" src={postgresql} alt="postgresql elephant" />
      <h2>{window.navigator.platform}</h2>
    </div>
  )
}

interface StyleSheet {
  [name: string]: React.CSSProperties
}
const styles: StyleSheet = {
  parrafo: {
    marginLeft: '1vw',
    fontSize: 'calc(10px + 2vh)'
  }
}
export default Home